/* src/index.css */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #f7f8fb;
  color: #222;
}
.app-root {
  max-width: 900px;
  margin: 36px auto;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
  overflow: hidden;
}
.site-header {
  padding: 28px;
  text-align: center;
  background: linear-gradient(90deg, #fff, #f9fbff);
}
.hero-title {
  margin: 0;
  font-size: 28px;
  color: #0b76ef;
}
.hero-desc {
  margin-top: 8px;
  color: #666;
}
.site-main {
  padding: 20px 28px;
}
.contact-card {
  padding: 16px;
  border-top: 1px solid #eee;
  background: #fcfcff;
  border-radius: 6px;
}
.site-footer {
  padding: 12px 20px;
  font-size: 13px;
  color: #666;
  text-align: right;
  background: #fafafa;
}
